import { Component, OnInit } from '@angular/core';
import {NgbDateStruct, NgbCalendar} from '@ng-bootstrap/ng-bootstrap';
import { formatDate } from '@angular/common';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-diary-feeling',
	templateUrl: './diary-feeling.component.html',
	styleUrls: ['./diary-feeling.component.css']
})
export class DiaryFeelingComponent implements OnInit {

	inputDates; //input to chart
	startdate;
	enddate;
	//判斷是否送出
	submitted = false;

	Form = this.formBuilder.group({
		start: ['',[Validators.required]],
		end: ['',[Validators.required]],
	});

	// convenience getter for easy access to form fields
	get start() { return this.Form.get('start'); }
	get end() { return this.Form.get('end'); }

	// scrollbar套件設定
	public scrollbarOptions = { axis: 'y', theme: 'minimal-dark' };

	constructor(private formBuilder:FormBuilder, private router:Router, private route:ActivatedRoute) { }

	ngOnInit() {
		let today = new Date();
		let lastWeek = new Date();

		lastWeek.setDate (lastWeek.getDate() - 7);
		this.inputDates = {
			'start':lastWeek,
			'end':new Date()
		};

		this.startdate = lastWeek.getTime();
		this.enddate = today.getTime();
		console.log(this.startdate);
		console.log(this.enddate);
	}


	//[datepicker] Child模板事件發生時，會呼叫此方法回傳值
	onListenStart($event: any) {
	    let strFormChild = $event as Date;
	    this.startdate = strFormChild.getTime();
	}


	//[datepicker] Child模板事件發生時，會呼叫此方法回傳值
	onListenEnd($event: any) {
	    let strFormChild = $event as Date;
	    this.enddate = strFormChild.getTime();
	}


	//submit form
	onSubmit() {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.Form.invalid || (this.startdate > this.enddate) || (this.enddate - this.startdate > 2592000000)){
			return;
		}

  		
  		console.log(this.startdate);
  		console.log(this.enddate);

  		this.inputDates = {
  			'start':this.startdate,
  			'end':this.enddate,
  		};

  		//this.router.navigate(['home/diary/my', this.startdate, this.enddate]);
  		
  		
	}

}
