import { Component, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { DiaryCreateComponent } from "../diary-create/diary-create.component";

@Component({
	selector: 'app-diary-menu',
	templateUrl: './diary-menu.component.html',
	styleUrls: ['./diary-menu.component.css']
})
export class DiaryMenuComponent implements OnInit {

	date = new Date().getTime();

	constructor(public dialog: MatDialog) { }

	ngOnInit() {
	}


	gotoCreate(){
		this.openDialog(this.date);
	}


	 //開啟pop up
    openDialog(data){
    	const dialogRef = this.dialog.open(DiaryCreateComponent, {
		  	width: '600px',
		  	data: {date: data}
		});
    }
}
