import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';

declare var Chart: any;

@Component({
	selector: 'app-psychology-result',
	templateUrl: './psychology-result.component.html',
	styleUrls: ['./psychology-result.component.css']
})
export class PsychologyResultComponent implements OnInit {

	chart;
	chart_type;
	score_sum;
	title;
	result;
	hashtags;
	ans_id;
	constructor(private route: ActivatedRoute, private http:HttpService) { }

	ngOnInit() {
		this.route.params.subscribe(params => {
			this.ans_id = params['answer_id'];
    		this.getChart(params['answer_id']);
    		this.getResult(params['test_id']);
    		this.getHashtag(params['answer_id']);
    	});
	}


	//取得結果說明
	getResult(id){
		let url:string;

		url = `${Config.APIUrl}/api/content/psychological-test/show?id=${id}&viewed=0`;
		this.http.getHttp(url).subscribe(
			res => {
				this.title = res.test.title;
				this.result = res.test.result_content;
				this.chart_type = res.test.chart_setting;
				console.log(res);

				//加入心理測驗圖表function
				if(this.chart_type=="radar"){
					//進行chatjs 雷達圖
					this.setChart("radar");
				}

				if(this.chart_type=="polarArea"){
					//進行chatjs 扇形圖
					this.setChartPolarArea();
				}

				if(this.chart_type=="bar"){
					//進行chatjs 水平長條圖
					this.setChartBar();
				}

				if(this.chart_type=="score"){
					//分數
					this.setChartScore();
				}

			}
		);
	}


	//取得結果圖表data
	getChart(id){
		let url:string;

		url = `${Config.APIUrl}/api/member/psychological-test-answer/chart-data?id=${id}`;
		this.http.getHttp(url).subscribe(
			res => {

				this.chart = res;
				console.log(res);

				//加入心理測驗圖表function
			}
		);
	}


	//取得結果hashtag
	getHashtag(id){
		let url:string;

		url = `${Config.APIUrl}/api/member/psychological-test-answer/show?id=${id}`;
		this.http.getHttp(url).subscribe(
			res => {
				this.hashtags = res.hash_tags;
				console.log(res);
			}
		);
	}


	// 雷達圖
	setChartRadar(){
		new Chart('chart', {
	    type: 'radar',
	    data: {
	    	labels: this.chart.labels,
				datasets: [{
					label: this.title,
					backgroundColor: 'rgba(222,186,168,0.5)',
					borderColor: 'rgb(222,186,168)',
					pointBackgroundColor: 'rgb(222,186,168)',
					data: this.chart.datas
				}]
			},
			options: {
				aspectRatio: 1.15,
				scale: {
					ticks: {
						beginAtZero: true
					}
				}
			}
		});
	}


	// 扇形圖
	setChartPolarArea(){
		new Chart('chart', {
			type: 'polarArea',
	    data: {
				labels: this.chart.labels,
				datasets: [{
					label: this.title, // for legend
					data: this.chart.datas,
					backgroundColor: [
						'rgba(246,236,230,0.5)',
						'rgba(222,186,168,0.5)',
						'rgba(133,101,109,0.5)',
						'rgba(62,51,66,0.5)',
					],
				}],
			},
			options: {
				aspectRatio: 0.99,
				responsive: true,
				legend: {
					position: 'top',
				},
				scale: {
					ticks: {
						beginAtZero: true
					},
					reverse: false
				},
				animation: {
					animateRotate: false,
					animateScale: true
				}
			}
		});
	}


	// 水平長條圖
	setChartBar(){
		new Chart('chart', {
			type: 'horizontalBar',
			data: {
				labels: this.chart.labels,
				datasets: [{
					label: this.title,
					backgroundColor: [
						'rgba(246,236,230,0.5)',
						'rgba(222,186,168,0.5)',
						'rgba(133,101,109,0.5)',
						'rgba(62,51,66,0.5)',
					],
					borderColor: [
						'rgba(246,236,230,1)',
						'rgba(222,186,168,1)',
						'rgba(133,101,109,1)',
						'rgba(62,51,66,1)',
					],
					borderWidth: 1,
					data: this.chart.datas,
				}]
			},
			options: {
				aspectRatio: 1.8,
				responsive: true,
				legend: {
					position: 'top',
				},
				scales: {
				  xAxes: [{
				    ticks: {
				      min: 0,
				    }
				  }]
				}
			}
		});
	}
	


	// 分數
	setChartScore(){
		const scores = this.chart.datas;
		const reducer = (accumulator, currentValue) => accumulator + currentValue;
		
		this.score_sum = scores.reduce(reducer);
	}


	// 設定chartjs圖表
	setChart(type){
		// 雷達圖
		if(type=="radar")
			this.setChartRadar();

		// 扇形圖
		if(type=="polarArea")
			this.setChartPolarArea();

		// 水平長條圖
		if(type=="bar")
			this.setChartBar();

		// 分數
		if(type=="score")
			this.setChartScore();
	}

}
