import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import swal, { SweetAlertOptions } from 'sweetalert2';

@Component({
	selector: 'app-diary-edit',
	templateUrl: './diary-edit.component.html',
	styleUrls: ['./diary-edit.component.css']
})
export class DiaryEditComponent implements OnInit {

	diary;
	img_url;
	image;
	submitted = false;

	Form = this.formBuilder.group({
		title: [''],
		image: [''],
		content: [''],
		date: [''],
		id: [''],
	});

	// convenience getter for easy access to form fields
	get title() { return this.Form.get('title'); }

	//傳給FILE UPLOAD的參數
	inputImageUpload = {
		isCheckImageType: true, //是否檢查圖片副檔名 
		isCheckFileSize: true,  //是否檢查圖片檔案
		isCheckVideoType: false,//是否檢查video副檔名 
		isCheckVideoTime: false,//是否檢查video時間 
		width: 300,
	};

	//傳給ckeditor
	inputCkeditor;

	// scrollbar套件設定
	public scrollbarOptions = { axis: 'y', theme: 'minimal-dark' };

	constructor(private router:Router, private route:ActivatedRoute, private http:HttpService, private formBuilder: FormBuilder) {
		//this.getDiary();
	}

	ngOnInit() {
		this.route.params.subscribe(params => {
    		this.getDiary(params['id']);
    	});
		
	}


	//[fileupload] Child模板事件發生時，會呼叫此方法回傳值
	onListenFile($event: any) {
		console.log($event);
		this.image = $event;
		this.img_url = null;
	}


	//[ckeditor] Child模板事件發生時，會呼叫此方法回傳值
	onListenCkeditor($event: any) {
		console.log($event);
		this.diary.content = $event;
		//this.img_url = null;
	}


	//取得單一日記資訊
	getDiary(id){
		//const id = +this.route.snapshot.paramMap.get('id');
		const url = `${Config.APIUrl}/api/member/diary/show?id=${id}`;
		this.http.getHttp(url).subscribe(
				res => {
					this.diary = res;
					if(res.image=="")
						this.img_url = Config.diaryCover;
					else
						this.img_url = `${Config.FileUrl}${res.image}`;
					this.image = res.image;
					this.inputCkeditor = res.content;
					console.log(this.diary);
				}
			);
	}



	//submit form
	onSubmit(routeLink) {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;
		console.log(routeLink);

		//表單沒有驗證成功 沒有動作
		if(this.Form.invalid){
			return;
		}

  		let params:FormData = new FormData();
  		Object.keys(this.Form.value).map(key=> params.append(key,this.Form.value[key]));
  		params.append('_method','PUT');

  		const url = `${Config.APIUrl}/api/member/diary/draft`;

  		//this.http.loadingOpen = false;
	  	this.http.postHttp(url,params)
		    .subscribe(res => {
		    	if(routeLink=="save"){
				    swal({
						title: '編輯成功!',
						text: '日記儲存成功',
						type: 'success',
					});
				}else if(routeLink=="next"){
					this.router.navigate(['../../posttest/', this.diary.id],  { relativeTo: this.route });
				}
		    },
		    err => console.error(err),
		    //() => this.http.loadingOpen = true
		    );
	}

}
